import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { post } from '../post.interface';
import { AppService } from 'src/app/app.service';
import { EditorComponent } from 'src/app/share/editor/editor.component';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})
export class PostComponent {
  @Output() closeDia: EventEmitter<any> = new EventEmitter();
  @ViewChild(EditorComponent) editor: EditorComponent;
  @Input("data") post: post;
  constructor(public app: AppService) { }
  updatePost() {
    this.app.wait();
    this.editor.editor.save().then((outputData) => {
      if (outputData.blocks.length == 0) {
        this.app.wait(false);
        this.app.log("بعض البيانات مفقودة");
        return;
      }
      this.app.http.put(`/api/posts/${this.post.userId}/${this.post.id}`, {
        content: outputData.blocks,
      }).subscribe(
        res => {
          this.post.content = outputData.blocks
          this.app.wait(false);
        },
        err => { }
      )
    }).catch((error) => { });
  }
  close() {
    this.closeDia.emit();
  }
}