import { Component, ViewChild } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { EditorComponent } from 'src/app/share/editor/editor.component';
@Component({
  selector: 'app-add-post',
  templateUrl: './add-post.component.html',
  styleUrls: ['./add-post.component.scss']
})
export class AddPostComponent {
  constructor(public app: AppService) { }
  @ViewChild(EditorComponent) editor: EditorComponent;
  save(star: number, header: string) {
    if (header == "") {
      this.app.log("بعض البيانات مفقودة");
      return
    }
    this.app.wait();
    this.editor.editor.save().then((outputData) => {
      if (outputData.blocks.length == 0) {
        this.log();
        return;
      }
      this.app.http.post(`/api/posts/${this.app.user.id}`, {
        header: header,
        img: this.app.user.img,
        star: star,
        content: outputData.blocks,
        userId: this.app.user.id,
        auther: this.app.user.name
      }).subscribe(
        () => {
          this.app.router.navigateByUrl("/blog");
          this.app.wait(false);
        },
        () => { }
      )
    }).catch(() => { });
  }
  private log() {
    this.app.wait(false);
    this.app.log("بعض البيانات مفقودة");
  }
}
