import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { post } from '../post.interface';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.scss']
})
export class PostsComponent implements OnInit {
  posts: post[] = [];
  selectPost: number;
  searchText: string;
  open: boolean = false;
  constructor(public app: AppService) { }
  ngOnInit(): void {
    this.getPosts()
  }
  addPost() {
    if (!this.app.isLogin()) {
      this.app.dialogService.alert(
        "ملحوظة هامة",
        "يجب ان تكون مسجل دخول لكى تتمكن من اضافة منشور",
        "",
        "تمام",
        "alert")
    } else {
      this.app.router.navigateByUrl("blog/newPost");
    }
  }
  getPosts() {
    this.app.wait();
    this.app.http.get<post[]>("/api/posts").subscribe(
      res => {
        this.posts = res;
        this.app.wait(false);
      },
      err => { }
    )
  }
  deletePost(id: string) {
    this.app.dialogService.confirm(
      "تاكيد حذف المنشور",
      "هل انت متاكد من حذف المنشور",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            this.app.wait();
            this.app.http.delete(`/api/posts/${id}`).subscribe(
              res => {
                this.ngOnInit()
                this.app.wait(false);
              },
              err => {
                this.app.wait(false)
              }
            )
          }
        }
      )
  }
  login() {
    if (this.app.isLogin()) {
      this.app.dialogService.confirm(
        "ملحوظة هامة",
        "انت بالفعل مسجل الدخول هل تريد التسجيل من حساب اخر",
        "نعم",
        "لا").subscribe(
          res => {
            if (res == true) {
              this.app.router.navigateByUrl("login");
            }
          }
        )
    } else {
      this.app.router.navigateByUrl("login");
    }
  }
  openPost(i: number) {
    this.selectPost = i;
    this.open = true;
  }
}
